<template>
    <div id="app">
        <div id="map_container"></div>
        <div class="RightBox4">
            <img src="../img/right3.png" class="img">
            <div class="boxTitle">分布对比</div>
            <div class="boxmain">
                <!--图表-->
                <br>
                <div class="boxhang1">-----------------热力图----------------------</div>
                <!-- 热力图 -->
                <button @click="loadHeatmap2012" style="width:90%;">场景 2012 </button>
                <button @click="loadHeatmap2014" style="width:90%;">场景 2014 </button>
                <button @click="loadHeatmap2016" style="width:90%;">场景 2016 </button>
                <button @click="loadHeatmap2018" style="width:90%;">场景 2018 </button>
                <button @click="loadHeatmap2020" style="width:90%;">场景 2020 </button>
                <button @click="loadHeatmap2022" style="width:90%;">场景 2022 </button>
                <br>
                <div class="boxhang1">-----------------热力网格----------------------</div>
                <!-- 热力柱网格 -->
                <button @click="loadHeatGrid2012" style="width:90%;">场景 2012 </button>
                <button @click="loadHeatGrid2014" style="width:90%;">场景 2014 </button>
                <button @click="loadHeatGrid2016" style="width:90%;">场景 2016 </button>
                <button @click="loadHeatGrid2018" style="width:90%;">场景 2018 </button>
                <button @click="loadHeatGrid2020" style="width:90%;">场景 2020 </button>
                <button @click="loadHeatGrid2022" style="width:90%;">场景 2022 </button>
            </div>
        </div>
    </div>
</template>

<script>
/* global BMapGL, BMAP_ANCHOR_BOTTOM_RIGHT, BMAP_ANCHOR_BOTTOM_LEFT, mapvgl*/
import axios from 'axios';

export default {
    data() {
        return {
            map: null,
            view: null, // 将 view 提升到 data 中
            grid: null, // 在 initializeMap 中初始化
            heatmap: null, // 在 initializeMap 中初始化
            getGridDataRange: {},
        };
    },
    mounted() {
        this.initializeMap();
    },
    methods: {
        initializeMap() {
            this.map = new BMapGL.Map("map_container");
            this.map.centerAndZoom(new BMapGL.Point(114.305392, 30.593098), 11);

            this.map.setMapStyleV2({
                styleId: '2cd2d0647854f1b38f743ca6f98ceacd'
            });
            this.map.enableScrollWheelZoom(true);
            
            // 控件添加
            var navi3DCtrl = new BMapGL.NavigationControl3D({
                anchor: BMAP_ANCHOR_BOTTOM_RIGHT,
                offset: new BMapGL.Size(50, 100)
            });
            this.map.addControl(navi3DCtrl);

            var scaleCtrl = new BMapGL.ScaleControl();
            this.map.addControl(scaleCtrl);
            var zoomCtrl = new BMapGL.ZoomControl({
                anchor: BMAP_ANCHOR_BOTTOM_RIGHT,
                offset: new BMapGL.Size(20, 100)
            });
            this.map.addControl(zoomCtrl);
            
            var locationControl = new BMapGL.LocationControl({
                anchor: BMAP_ANCHOR_BOTTOM_LEFT,
                offset: new BMapGL.Size(10, 100)
            });
            this.map.addControl(locationControl);

            // 初始化 View
            this.view = new mapvgl.View({
                map: this.map,
            });

            // 初始化 HeatGridLayer
            this.grid = new mapvgl.HeatGridLayer({
                max: 20, // 基于点的数量，可能需要调整
                min: 1,  // 最小值为1个点
                gridSize: 300, // 网格大小（米）
                gradient: {
                    0: 'rgb(50, 50, 256)',
                    0.3: 'rgb(178, 202, 256)',
                    1: 'rgb(250, 250, 256)'
                },
                riseTime: 1800,
                maxHeight: 1500,
                minHeight: 50
            });
            this.view.addLayer(this.grid);

            // 初始化 HeatmapLayer
            this.heatmap = new mapvgl.HeatmapLayer({
                size: 300, // 热力点半径（米）
                max: 20, // 基于点的密度，可能需要调整
                height: 0,
                unit: 'm',
                gradient: {
                    0.25: 'rgba(0, 0, 255, 1)',
                    0.55: 'rgba(0, 255, 0, 1)',
                    0.85: 'rgba(255, 255, 0, 1)',
                    1: 'rgba(255, 0, 0, 1)'
                }
            });
            this.view.addLayer(this.heatmap);
            
            // 默认加载一个场景 (更新为2012)
            this.loadHeatmap2012();
        },
        toggleLayerVisibility(layer, isVisible) {
            if (!layer) return;
            if (isVisible) {
                layer.show();
            } else {
                layer.hide();
            }
        },

        /**
         * 智能加载数据方法
         * ... (此处省略 loadData 方法，与您提供的一致)
         */
        loadData(url, layer) {
            if (!layer) {
                console.error('图层未定义');
                return;
            }
            
            axios.get(url)
                .then(response => {
                    let points = [];
                    
                    // 格式 1: 预格式化数据 (如 11.json, 22.json)
                    if (response.data.result && response.data.result.data[0] && response.data.result.data[0].bound) {
                        console.log(`加载预格式化数据: ${url}`);
                        points = response.data.result.data[0].bound.map(item => ({
                            lng: parseFloat(item[0]),
                            lat: parseFloat(item[1]),
                            count: item[2] // 使用文件中的 count
                        }));
                    }
                    // 格式 2: 原始点数据 (如 fin2020.json)
                    else if (Array.isArray(response.data)) {
                        console.log(`加载原始点数据: ${url}`);
                        points = response.data.map(item => ({
                            lng: parseFloat(item.longitude),
                            lat: parseFloat(item.latitude),
                            count: 1 // 关键：设置 count 为 1，让图层去聚合
                        }));
                    } else {
                        console.error('未知的JSON数据格式:', url);
                        return;
                    }

                    // 过滤掉无效坐标
                    const validPoints = points.filter(item => !isNaN(item.lng) && !isNaN(item.lat) && item.lng && item.lat);

                    // 转换为 MapVGL GeoJSON 格式
                    const data = validPoints.map(item => ({
                        geometry: {
                            type: 'Point',
                            coordinates: [item.lng, item.lat],
                        },
                        properties: {
                            count: item.count,
                        },
                    }));

                    layer.setData(data);
                })
                .catch(error => {
                    console.error(`获取数据时发生错误 (${url}):`, error);
                });
        },

        // --- 热力图方法 ---
        loadHeatmap2012() {
            this.loadData('/2012.json', this.heatmap);
            this.toggleLayerVisibility(this.heatmap, true);
            this.toggleLayerVisibility(this.grid, false);
            this.map.setHeading(0);
            this.map.setTilt(0);
        },
        loadHeatmap2014() {
            this.loadData('/2014.json', this.heatmap);
            this.toggleLayerVisibility(this.heatmap, true);
            this.toggleLayerVisibility(this.grid, false);
            this.map.setHeading(0);
            this.map.setTilt(0);
        },
        loadHeatmap2016() {
            this.loadData('/2016.json', this.heatmap);
            this.toggleLayerVisibility(this.heatmap, true);
            this.toggleLayerVisibility(this.grid, false);
            this.map.setHeading(0);
            this.map.setTilt(0);
        },
        loadHeatmap2018() {
            this.loadData('/2018.json', this.heatmap);
            this.toggleLayerVisibility(this.heatmap, true);
            this.toggleLayerVisibility(this.grid, false);
            this.map.setHeading(0);
            this.map.setTilt(0);
        },
        loadHeatmap2020() {
            this.loadData('/fin2020_bd09.json', this.heatmap);
            this.toggleLayerVisibility(this.heatmap, true);
            this.toggleLayerVisibility(this.grid, false);
            this.map.setHeading(0);
            this.map.setTilt(0);
        },
        loadHeatmap2022() {
            this.loadData('/fin2022_bd09.json', this.heatmap);
            this.toggleLayerVisibility(this.heatmap, true);
            this.toggleLayerVisibility(this.grid, false);
            this.map.setHeading(0);
            this.map.setTilt(0);
        },

        // --- 热力网格方法 ---
        loadHeatGrid2012() {
            this.loadData('/2012_bd09.json', this.grid);
            this.loadData('/2012_bd09.json', this.heatmap);
            this.toggleLayerVisibility(this.heatmap, true);
            this.toggleLayerVisibility(this.grid, true);
            this.map.setHeading(30);
            this.map.setTilt(45);
        },
        loadHeatGrid2014() {
            this.loadData('/2014_bd09.json', this.grid);
            this.loadData('/2014_bd09.json', this.heatmap);
            this.toggleLayerVisibility(this.heatmap, true);
            this.toggleLayerVisibility(this.grid, true);
            this.map.setHeading(30);
            this.map.setTilt(45);
        },
        loadHeatGrid2016() {
            this.loadData('/2016_bd09.json', this.grid);
            this.loadData('/2016_bd09.json', this.heatmap);
            this.toggleLayerVisibility(this.heatmap, true);
            this.toggleLayerVisibility(this.grid, true);
            this.map.setHeading(30);
            this.map.setTilt(45);
        },
        loadHeatGrid2018() {
            this.loadData('/2018_bd09.json', this.grid);
            this.loadData('/2018_bd09.json', this.heatmap);
            this.toggleLayerVisibility(this.heatmap, true);
            this.toggleLayerVisibility(this.grid, true);
            this.map.setHeading(30);
            this.map.setTilt(45);
        },
        loadHeatGrid2020() {
            this.loadData('/fin2020_bd09.json', this.grid);
            this.loadData('/fin2020_bd09.json', this.heatmap);
            this.toggleLayerVisibility(this.heatmap, true);
            this.toggleLayerVisibility(this.grid, true);
            this.map.setHeading(30);
            this.map.setTilt(45);
        },
        loadHeatGrid2022() {
            this.loadData('/fin2022_bd09.json', this.grid);
            this.loadData('/fin2022_bd09.json', this.heatmap);
            this.toggleLayerVisibility(this.heatmap, true);
            this.toggleLayerVisibility(this.grid, true);
            this.map.setHeading(30);
            this.map.setTilt(45);
        },
    },
};
</script>

<style scoped>
#app {
    width: 100%;
    height: 100vh;
    margin: 0;
    padding: 0;
}

#map_container {
    width: 100%;
    height: 100%;
    margin: 0;
}

.RightBox4 {
    position: absolute;
    height: 100%;
    width: 27%;
    z-index: 10;
    top: 0;
    right: 0;
    pointer-events: auto;
    background-color: rgba(20, 20, 40, 0.8); /* 添加一个半透明背景，使其更易读 */
    padding: 10px;
    border-radius: 8px;
    box-sizing: border-box;
    /* 确保内容溢出时可以滚动 */
    overflow-y: auto;
}
.boxhang1 {
  position: relative;
  float: left;
  margin-bottom: 10px;
  width: 100%;
  height: 1.6vw;
  text-align: center;
  line-height: 1.6vw;
  font-size: .9vw;
  color: #0efcff;
  letter-spacing: .1vw;
}

/* 样式化按钮使其更清晰 */
button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 8px 12px;
    margin-bottom: 8px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
}
button:hover {
    background-color: #0056b3;
}
</style>


