<template>
  <div id="app">
    <div id="l-map"></div>
    <div class="LeftBox">
      <img src="../img/left.png" class="img">
      <!-- 四个角描边 end -->
      <div class="boxTitle">详细信息</div>
      <div class="boxmain">
        <!--图表-->
        <div class="boxhang1">--------------------------------</div>
        
        <!-- 更新：名称部分 -->
        <div class="info-section">
          <p class="info-title">名称：</p>
          <p class="info-content">{{ buildingInfo.name }}</p>
        </div>
        
        <div class="boxhang">--------------------------------</div>

        <!-- 更新：相关信息部分 -->
        <div class="info-section-details">
          <p class="info-title">地址：</p>
          <p class="info-content">{{ buildingInfo.address }}</p>
          <p class="info-title">电话：</p>
          <p class="info-content">{{ buildingInfo.phone }}</p>
          <p class="info-title">类型：</p>
          <p class="info-content">{{ buildingInfo.type }}</p>
        </div>

      </div>
    </div>
    <!-- <div class="RightBox">
      <img src="../img/right3.png" class="img">
      <div class="boxTitle">路径规划</div>
      <div class="boxmain">
        <div class="navigate">
          <input v-model="startLocation" placeholder="起点" />
          <input v-model="endLocation" placeholder="终点" />
          <button @click="navigate">导航</button>
        </div>

        <div id="r-result"></div>
      </div>
    </div> -->
  </div>
</template>

<script>
/* global BMapGL, BMAP_ANCHOR_BOTTOM_RIGHT, BMAP_ANCHOR_BOTTOM_LEFT */
// 新增：导入 axios 用于加载 JSON
import axios from 'axios';

export default {
  data() {
    return {
      map: null, // 新增：保存 map 实例
      walking: null, // 新增：保存 walking 实例
      startLocation: '',
      endLocation: '',
      buildingInfo: {
        name: '暂无信息', // 默认值
        address: 'N/A',
        phone: 'N/A',
        type: 'N/A',
        image: '', // 尽管不显示，但保留数据结构
      },
    };
  },
  methods: {
    // 新增：地图初始化方法
    initializeMap() {
      this.map = new BMapGL.Map("l-map");
      this.map.centerAndZoom(new BMapGL.Point(114.305392, 30.593098), 11); // 默认中心点（武汉市）
      this.map.enableScrollWheelZoom(true);
      this.map.setMapStyleV2({
        styleId: '2cd2d0647854f1b38f743ca6f98ceacd'
      });



      // --- 控件添加 ---
      var navi3DCtrl = new BMapGL.NavigationControl3D({
        anchor: BMAP_ANCHOR_BOTTOM_RIGHT,
        offset: new BMapGL.Size(50, 100)
      });
      this.map.addControl(navi3DCtrl);

      var scaleCtrl = new BMapGL.ScaleControl();
      this.map.addControl(scaleCtrl);
      var zoomCtrl = new BMapGL.ZoomControl({
        anchor: BMAP_ANCHOR_BOTTOM_RIGHT,
        offset: new BMapGL.Size(20, 100)
      });
      this.map.addControl(zoomCtrl);
      
      var locationControl = new BMapGL.LocationControl({
        anchor: BMAP_ANCHOR_BOTTOM_LEFT,
        offset: new BMapGL.Size(10, 100)
      });
      this.map.addControl(locationControl);

      // --- 初始化路径规划 ---
      this.walking = new BMapGL.WalkingRoute(this.map, {
        renderOptions: { map: this.map, panel: "r-result", autoViewport: true },
      });

      // --- 新增：加载酒店数据 ---
      this.loadHotelData();
    },

    // 新增：加载酒店数据并添加到地图
    loadHotelData() {
      axios.get('/2018.json')
        .then(response => {
          const hotels = response.data;
          if (!Array.isArray(hotels)) {
            console.error('JSON 数据格式不正确，应为数组。');
            return;
          }

          // --- 新增：随机抽取一半的数据 ---
          // 更改：使用索引 % 2 来确保每次加载的数据都相同，而不是随机
          const sampledHotels = hotels.filter((_, index) => index % 5 === 0);
          console.log(`总共 ${hotels.length} 家酒店, 固定加载 ${sampledHotels.length} 家。`);
          // --- 结束新增 ---

          // 更改：现在遍历随机抽取后的 "sampledHotels" 数组
          sampledHotels.forEach(hotel => {
            // 确保经纬度有效
            const longitude = parseFloat(hotel.longitude);
            const latitude = parseFloat(hotel.latitude);

            if (!isNaN(longitude) && !isNaN(latitude)) {
              const point = new BMapGL.Point(longitude, latitude);
              const marker = new BMapGL.Marker(point);
              this.map.addOverlay(marker);

              // 为标记点添加点击事件
              marker.addEventListener('click', () => {
                // 更新：匹配新的 buildingInfo 结构
                this.buildingInfo = {
                  name: hotel.name || '未知名称',
                  address: hotel.ADDRESS || 'N/A',
                  phone: hotel.TELEPHONE || 'N/A',
                  type: hotel.SUBTYPE || 'N/A',
                  image: '', // 2018.json 中没有图片字段
                };
              });
            }
          });
        })
        .catch(error => {
          console.error('加载 /2018.json 失败:', error);
        });
    },

    // 优化：navigate 方法只负责搜索
    navigate() {
      if (this.walking) {
        this.walking.search(this.startLocation, this.endLocation);
      } else {
        console.error('导航服务尚未初始化。');
      }
    },
  },
  mounted() {
    // 更改：在 mounted 时调用 initializeMap
    this.initializeMap();
  },
};
</script>

<style>
#app {
  width: 100%;
  height: 100vh;
  padding: 0;
  margin: 0;
  font-family: "微软雅黑";
}

#l-map {
  width: 100%;
  height: 100%;
  margin: 0;
}


#r-result,
#r-result table {
  width: 90%;
  height: 100%;
  overflow-y: auto;
  /* Add this property to enable vertical scrollbar if content exceeds the height */
}

.RightBox {
  position: absolute;
  top: 0;
  right: 0;
  width: 20vw;
  /* 屏幕宽度的 20% */
  height: 50vh;
  /* 占据整个视窗高度 */
  z-index: 2;
}


.img {
  position: relative;
  height: 100%;
  width: 100%;
}

.boxTitle {
  position: absolute;
  z-index: 3;
  left: 40%;
  top: 2%;
  font-size: 20px; /* 统一样式 */
}

.boxmain {
  position: absolute;
  z-index: 3;
  top: 15%;
  height: 80%; /* 统一 */
  width: 90%; /* 统一 */
  display: flex;
  flex-direction: column;
  padding: 10px;
  overflow: hidden;
  left: 5%; /* 居中 */
  box-sizing: border-box;
}

.navigate {
  background-color: #ffffff;
  color: rgba(27, 142, 236, 1);
  font-size: 14px;
  border: 1px solid rgba(27, 142, 236, 1);
  width: 100%; /* 调整宽度 */
  box-sizing: border-box; /* 包含 padding/border */
}
.navigate input {
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 5px;
}
.navigate button {
  width: 100%;
  box-sizing: border-box;
}


/* 始终处于左上角 */
.LeftBox {
  position: absolute;
  height: 70vh;
  width: 20%;
  z-index: 2;
  top: 0;
  left: 0;
}

/* .boxmain (LeftBox) - 在 .boxmain 通用样式中已定义 */


/* 移除 .boxpassage 样式，使用新的 info-section 样式 */
.info-section {
  width: 100%;
  margin-bottom: 10px;
  text-align: left;
  font-size: .9vw;
  color: #0efcff;
  letter-spacing: .1vw;
  word-wrap: break-word;
}
.info-section-details {
  width: 100%;
  flex-grow: 1; /* 占据剩余空间 */
  overflow-y: auto; /* 内容溢出时滚动 */
  text-align: left;
  font-size: .9vw;
  color: #0efcff;
  letter-spacing: .1vw;
  word-wrap: break-word;
}

.info-title {
  font-weight: bold;
  color: #ffffff; /* 标题使用白色，更突出 */
  margin: 5px 0 2px 0;
  font-size: 1vw;
}

.info-content {
  color: #0efcff;
  margin: 0 0 10px 0;
  padding-left: 10px; /* 内容缩进 */
  line-height: 1.4;
}


.boxhang {
  position: relative;
  float: left;
  margin-bottom: 10px; /* 调整间距 */
  width: 100%;
  height: 1.6vw;
  text-align: center;
  line-height: 1.6vw;
  font-size: .9vw;
  color: #0efcff;
  letter-spacing: .1vw;
}

.boxhang1 {
  position: relative;
  float: left;
  margin-bottom: 10px;
  width: 100%;
  height: 1.6vw;
  text-align: center;
  line-height: 1.6vw;
  font-size: .9vw;
  color: #0efcff;
  letter-spacing: .1vw;
}
</style>

